var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5"},[_vm._m(0),_vm._m(1),_vm._m(2),_c('div',{staticClass:"row justify-content-center align-items-start mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('TabsA',{staticClass:"color-acento-contenido grs"},[_c('div',{staticClass:"tarjeta color-acento-botones fnd-3 p-4",attrs:{"titulo":"Población"}},[_c('p',[_vm._v("En estadística, el término población, se refiere a la integración de datos que requieren ser organizados, analizados e interpretados. Expone Gamboa (2017), que la población no se refiere exclusivamente a seres humanos, sino que también puede corresponder a animales, muestras biológicas, expedientes, hospitales, objetos, familias, organizaciones, etc.; para estos últimos, podría ser más adecuado utilizar un término análogo, como universo de estudio. Entonces, en el objeto de estudio, el análisis de la información conlleva a estudiar datos identificados, estos datos los conocemos como población, un ejemplo de población puede ser la cantidad de personas o la cantidad de cosas, en un lugar específico con el fin de ser analizado.")])]),_c('div',{staticClass:"tarjeta color-acento-botones fnd-3 p-4",attrs:{"titulo":"Muestra"}},[_c('p',[_vm._v("La acción de realizar un muestreo de información, se refiere a la selección de una porción-parte-subconjunto de datos del universo o población que haya sido seleccionada, delimitada y ubicada cronológicamente, la cual es objeto de estudio en la investigación. El muestreo tiene por objetivo estudiar las relaciones existentes entre la distribución de una variable “y” en una población “z” y la distribución de esta variable en la muestra a estudio (Hernández Sampieri, Fernández-Collado, & Baptista-Lucio, 2006), a su vez también exponen que cuando se determina la muestra en una investigación, se toman dos decisiones fundamentales: la manera como van a seleccionarse los casos (participantes, eventos, episodios, organizaciones, productos, etc.) y el número de casos a incluir (tamaño de muestra). Estos dos elementos permiten claramente unificar esfuerzos mediante la centralización de información, logrando con estos tener datos que representen la integralidad de los resultados analizados y que conllevarán a las decisiones. ")])])])],1),_vm._m(3)]),_c('p',{staticClass:"mb-5"},[_vm._v("Por otra parte, para definir una población y tomar el tamaño de la muestra correctamente, se deben tener en cuenta algunos detalles.")]),_c('p',{staticClass:"mb-5"},[_vm._v("Analice atentamente cada uno de ellos:")]),_c('div',{staticClass:"row justify-content-center align-items-start mb-5"},[_vm._m(4),_c('div',{staticClass:"col-lg-8"},[_c('TabsC',{staticClass:"color-acento-contenido grs"},[_c('div',{staticClass:"tarjeta color-acento-botones fnd-3 p-4 mb-4",attrs:{"titulo":"Definición de la población"}},[_c('p',[_vm._v("Para definir una población se deben tener en cuenta características que terminan siendo determinantes para el desarrollo de la investigación. El primer criterio a tener en cuenta se refiere a la selección correcta de la población a estudiar porque, de acuerdo con la selección clara de su población, podrá dar una delimitación objetiva permitiendo la organización de los datos requeridos para el estudio. Es importante recordar que hay poblaciones con datos infinitos, pero también estas poblaciones se pueden delimitar con el objetivo de que sus datos sean finitos. Otra característica determinante que le permitirá ubicar, de manera correcta, los datos a ser estudiados es la ubicación cronológica. No es lo mismo estudiar una población determinada en diferentes momentos, porque entran a ponderar variables externas que influyen en los análisis estadísticos que se deseen adelantar.")])]),_c('div',{staticClass:"tarjeta color-acento-botones fnd-3 p-4 mb-4",attrs:{"titulo":"Tamaño correcto de la muestra"}},[_c('p',[_vm._v("Buscando cómo llegar de manera correcta al tamaño de muestra, la cual busca encontrar esa porción-parte-subconjunto de datos del universo o población que haya sido seleccionada, se deben buscar mecanismos para la reducción de costos en diferentes recursos, como los son económicos, mano de obra y materiales, entre otros, siendo el muestreo un mecanismo que selecciona y delimita la recolección de los datos, porque se puede realizar en menor tiempo y también permite el estudio en poblaciones muy grandes.")]),_c('p',[_vm._v("Para hallar el tamaño de muestra estadísticamente, se desarrollan métodos, por lo cual es importante identificar el nivel de confianza, siendo este un grado de certeza o probabilidad expresado en porcentajes con el que se pretende realizar la estimación de un parámetro a través de un estadístico muestral “fórmula”. ")])])])],1)]),_vm._m(5)])],1)
